class UsersInit {
	constructor( editor ) {
		this.editor = editor;
	}

	static get pluginName() {
		return 'UsersInit';
	}

	static get requires() {
		return [ 'Users' ];
	}

	init() {
		const users = this.editor.plugins.get( 'Users' );

		users.addUser( appData.user );

		users.defineMe( appData.userId );
	}
}

ClassicEditor.create( document.querySelector( '#editor' ), {
	extraPlugins: [ UsersInit, RevisionHistoryAdapter ],

	toolbar: {
		items: [
			'undo', 'redo',
			'|', 'revisionHistory',
			'|', 'trackChanges', 'comment', 'commentsArchive',
			'|', 'heading',
			'|', 'fontsize', 'fontfamily', 'fontColor', 'fontBackgroundColor',
			'-', 'bold', 'italic', 'underline',
			'|', 'link', 'insertImage', 'insertTable', 'blockQuote', 'codeBlock',
			'|', 'alignment',
			'|', 'bulletedList', 'numberedList'
		],
		shouldNotGroupWhenFull: true
	},
	image: {
		toolbar: [
			'imageStyle:inline',
			'imageStyle:block',
			'imageStyle:wrapText',
			'|',
			'imageTextAlternative',
			'ckboxImageEdit'
		]
	},
	ckbox: {
		allowExternalImagesEditing: [ /^data:/, 'origin', /ckbox/ ],
		forceDemoLabel: true,
        tokenUrl: 'https://api.ckbox.io/token/demo',
	},
	typing: {
		transformations: {
			extra: [
				{ from: ':)', to: '🙂' },
				{ from: ':+1:', to: '👍' },
				{ from: ':tada:', to: '🎉' }
			]
		}
	},
	table: {
		contentToolbar: [ 'tableColumn', 'tableRow', 'mergeTableCells', 'tableProperties', 'tableCellProperties' ],
		tableToolbar: [ 'bold', 'italic' ]
	},
	heading: {
		options: [
			{ model: 'paragraph', title: 'Paragraph', class: 'ck-heading_paragraph' },
			{ model: 'heading1', view: 'h1', title: 'Heading 1', class: 'ck-heading_heading1' },
			{ model: 'heading2', view: 'h2', title: 'Heading 2', class: 'ck-heading_heading2' },
			{ model: 'heading3', view: 'h3', title: 'Heading 3', class: 'ck-heading_heading3' }
		]
	},
	cloudServices: CS_CONFIG,
	licenseKey: LICENSE_KEY,
	ui: {
		viewportOffset: {
			top: window.getViewportTopOffsetConfig()
		}
	},
	sidebar: {
		container: document.querySelector( '#sidebar-container' )
	},
	revisionHistory: {
		editorContainer: document.querySelector( '#editor-container' ),
		viewerContainer: document.querySelector( '#revision-viewer-container' ),
		viewerEditorElement: document.querySelector( '#revision-viewer-editor' ),
		viewerSidebarContainer: document.querySelector( '#revision-viewer-sidebar' )
	},
	comments: {
		editorConfig: {}
	},
	mention: {
		feeds: [
			{
				marker: '@',
				feed: [ '@Barney', '@Lily', '@Marry Ann', '@Marshall', '@Robin', '@Ted' ]
			}
		]
	},
	initialData: appData.data
} ).then( editor => {
	document.querySelector( '.live-snippet__container' ).classList.add( 'loaded' );

	// Switch between inline, narrow sidebar and wide sidebar according to the window size.
	const row = document.querySelector( '.live-snippet__container' );
	const annotationsUIs = editor.plugins.get( 'AnnotationsUIs' );
	const sidebarElement = document.querySelector( '#sidebar-container' );

	editor.ui.view.listenTo( window, 'resize', refreshDisplayMode );
	refreshDisplayMode();
	function refreshDisplayMode() {

		if ( row.offsetWidth < 650 ) {
			sidebarElement.classList.remove( 'narrow' );
			sidebarElement.classList.add( 'hidden' );
			annotationsUIs.switchTo( 'inline' );
		}
		else if ( row.offsetWidth < 840 ) {
			sidebarElement.classList.remove( 'hidden' );
			sidebarElement.classList.add( 'narrow' );
			annotationsUIs.switchTo( 'narrowSidebar' );
		}
		else {
			sidebarElement.classList.remove( 'hidden', 'narrow' );
			annotationsUIs.switchTo( 'wideSidebar' );
		}
	}
	window.editor = editor;
/*
	window.attachTourBalloon( {
		target: window.findToolbarItem( editor.ui.view.toolbar,
			item => item.buttonView && item.buttonView.label && item.buttonView.label === 'Revision history'
		),
		text: 'Click to open revision history.',
		editor,
		tippyOptions: {
			placement: 'bottom-start'
		}
	} );
*/
} );
